import React, { useState } from 'react'
import Navigation from './Navigation'


function Header() {

    const [show, showMenu] = useState(false)

    return (
        <>

            <header>
                <div className="menu-icon" onClick={() => {  showMenu(!show) }}>
                    <img src="/assets/images/hamburger-menu-icon.png" alt="" />

                </div>
                <div className="company-logo">
                    <img src="/assets/images/company-logo.png" alt="" />
                </div>
                <Navigation />
            </header>

        </>
    )
}

export default Header
